import React, { useState } from 'react';
import './SurveyPage.css';
import { redirect } from 'react-router-dom';
import axios from 'axios';

function SurveyPage() {
  const questions = [
    { id: 'question1', text: '        .' },
    { id: 'question2', text: '        .' },
    { id: 'question3', text: '     .' },
    { id: 'question4', text: '       .' },
    { id: 'question5', text: '       .' },
    { id: 'question6', text: '     .' },
    { id: 'question7', text: '    .' },
    { id: 'question8', text: '      .' },
    { id: 'question9', text: '       .' },
    { id: 'question10', text: '        .' },
    { id: 'question11', text: '      .' },
    { id: 'question12', text: ', ,     .' },
    { id: 'question13', text: '      .' },
    { id: 'question14', text: '       .' },
    { id: 'question15', text: '        .' }
  ];
  
  const choices = ['', '', ''];
  const questionsPerPage = 5;

  const [formData, setFormData] = useState({});
  const [currentPage, setCurrentPage] = useState(1);
  const [unansweredQuestions, setUnansweredQuestions] = useState([]);
  const scrollRef = React.createRef();

  const handleChange = (question, value) => {
    setFormData(prevData => ({
      ...prevData,
      [question]: value
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    axios.post('http://43.201.19.87:8080/member/survey', formData, {
      headers: {
        'Content-Type': 'application/json'
      }
    })
      .then(response => {
        console.log('  :', response.data);
        //         .
      })
      .catch(error => {
        console.error('  :', error);
        //          .
      });
  };
  
  React.useEffect(() => {
    const defaultFormData = {};
    questions.forEach(question => {
      defaultFormData[question.id] = '';
    });
    setFormData(defaultFormData);
  }, []);

  const handleNextPage = () => {
    setCurrentPage(prevPage => prevPage + 1);
    scrollRef.current.scrollTop = 0;
  };

  const handlePrevPage = () => {
    setCurrentPage(prevPage => prevPage - 1);
    scrollRef.current.scrollTop = 0;
  };

  const handleChoiceClick = (questionId, choice) => {
    setFormData(prevData => ({
      ...prevData,
      [questionId]: choice
    }));
    setUnansweredQuestions(prevQuestions => prevQuestions.filter(question => question !== questionId));
  };

  const indexOfLastQuestion = currentPage * questionsPerPage;
  const indexOfFirstQuestion = indexOfLastQuestion - questionsPerPage;
  const currentQuestions = questions.slice(indexOfFirstQuestion, indexOfLastQuestion);

  React.useEffect(() => {
    const unanswered = currentQuestions.filter(question => !formData[question.id]);
    setUnansweredQuestions(unanswered.map(question => question.id));
    scrollRef.current.scrollTop = 0;
  }, [currentPage]);

  return (
    <div>
      <h1></h1>
      <form onSubmit={handleSubmit}>
        <div ref={scrollRef} className="survey-container">
        {currentQuestions.map(question => (
  <div key={question.id} className="text-center">
    <label htmlFor={question.id}>{question.text}:</label>
    <br />
    <div className="button-group">
      {choices.map(choice => (
        <button
          key={choice}
          className={`option-button ${formData[question.id] === choice ? 'active' : ''}`}
          onClick={() => handleChange(question.id, choice)} 
        >
          {choice}
                  </button>
                ))}
              </div>
              <br />
            </div>
          ))}
        </div>
        <div className="button-group">
          {currentPage > 1 && (
            <button type="button" onClick={handlePrevPage}>
               
            </button>
          )}
          {currentPage < Math.ceil(questions.length / questionsPerPage) && (
            <button type="button" onClick={handleNextPage}>
               
            </button>
          )}
        </div>
        {currentPage === Math.ceil(questions.length / questionsPerPage) && (
          <button type="submit"></button>
        )}
      </form>
    </div>
  );
}

export default SurveyPage;
