import Carousel from 'react-bootstrap/Carousel';

function UncontrolledExample() {
  return (
    <Carousel>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src="/first_page.png"
          alt="First slide"
        />
        <Carousel.Caption>
          <h3> </h3>
          <p>      !</p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src="/second_page.png"
          alt="Second slide"
        />

        <Carousel.Caption>
          <h3> </h3>
          <p>    !</p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src="/third_page.png"
          alt="Third slide"
        />

        <Carousel.Caption>
          <h3> </h3>
          <p>
                  !
          </p>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>
  );
}

export default UncontrolledExample;